            p.mb-0 La construccin de <i>software</i> est basada en fases que permiten incrementar el producto desde sus cimientos hasta obtener un producto base; esto es posible, ya que en el proceso se implementan diferentes estrategias y metodologas, el gran desafo est, en cmo construir un producto que se pueda validar rpidamente y que contenga los niveles adecuados de calidad. La naturaleza de los requisitos de <i>software</i> es cambiante, lo cual complica la precisin de los equipos, dilatando posiblemente el tiempo, los recursos y el presupuesto asignado.

