        p.mb-3 El aseguramiento de la calidad de <i>software</i> o <I>SQA</I>, es un rea importante en el proceso que busca estar en constante validacin de la calidad del producto <i>software</i>, por lo general en el establecimiento de un plan de aseguramiento de calidad.
          br
          br
