        p.mb-0.py-5.box-with-degrade-invert.color-acento-contenido Se define el concepto de calidad como un conglomerado de caractersticas de una entidad que son esenciales y que adems permiten calificar su valor. Otro concepto a nivel de empresa est relacionado con el conjunto de estndares y normas que debe tener un producto o servicio para ser reconocido y percibido en el mercado satisfaciendo las necesidades de los clientes.

    p.mb-5 Cuando se hace referencia a la Calidad de <i>Software</i> (QA) se entiende como el nivel que tienen los clientes para determinar segn su percepcin que el producto <i>software</i> satisface sus necesidades y expectativas. A nivel empresarial, la calidad de <i>software</i> hace referencia a las actividades que se establecen de manera comn para asegurar que el desarrollo de <i>software</i> en todos los proyectos es de calidad. Tambin se puede definir como la relacin coherente del producto <i>software</i> con los requerimientos funcionales y no funcionales.
      br
      br
      | El Aseguramiento de la Calidad de <i>Software</i> (<I>SQA</I>) se conoce como el conjunto de actividades, mtodos y tcnicas planificadas y sistemticas necesarias para asegurar que el producto <i>software</i> satisfar los requerimientos dados de calidad. Se encarga de garantizar que el producto haya sido construido siguiendo los procesos establecidos por la organizacin.
      br
      br
      | En el siguiente video se presente la breve historia del surgimiento del Aseguramiento de la Calidad de <i>Software</i> (<I>SQA</I>):

    .row.justify-content-center.align-items-center.caja-aux.color-acento-contenido.border.radius-50.py-4.px-3.mb-5
      .col-lg-10
        p.mb-0 Cabe aclarar que la calidad de <i>software</i> puede medirse una vez en la fase de implementacin o terminado completamente o cuando el producto est en produccin, no obstante, este hecho implicara amplios costos para la compaa de <i>software</i> a diferencia de detectar y resolver los problemas en las fases iniciales como el diseo o en los requerimientos que generara una reduccin de los costos en cuanto a los cambios a realizar. Por lo tanto, es necesario tener en cuenta el proceso de <I>SQA</I> durante las etapas iniciales de la construccin del producto, puesto que es donde se definen las caractersticas que el producto debe garantizar para satisfacer las necesidades implcitas y explcitas. Por lo tanto, se debe tener en cuenta que si el diseo y los requerimientos no estn correctamente definidos, es decir que estn ambiguos, incompletos o son incoherentes, en este momento va a ser muy costosa su correccin.
