      .tarjeta.color-acento-contenido.p-4(x="26%" y="65.2%" )
        p.mb-0 <strong>1.</strong> Establecer estndares, procesos y planes con el objetivo de satisfacer las polticas de la organizacin y se ajusten a las necesidades de cada proyecto en particular para asegurar la calidad del producto <i>software</i>.

      .tarjeta.color-acento-contenido.p-4(x="29%" y="50.2%" )
