    p.mb-5 Adems, para realizar el Aseguramiento de la Calidad de <i>Software</i> (<I>SQA</I>) se deben tener en cuenta diversos principios bsicos como son:

    SlyderF.mb-5(columnas="col-lg-4 col-xl-4 d-flex")
      .tarjeta.color-acento-contenido-3.p-4.h-100
        .row.justify-content-center.mb-0.h-100
          .col-8.h-100
            img(src='@/assets/curso/tema2-img51.svg' alt='AvatarTop')
        p.text-center La calidad debe ser una constante a lo largo de todo el ciclo de vida del <i>software</i>. Por lo tanto, la calidad es una accin que se construye en la medida que el producto se va desarrollando, mas no cuando el producto est terminado, es por esta razn que todo el equipo o involucrados en el proceso deben tambin enfocarse en la calidad del producto.
      .tarjeta.color-acento-contenido-4.p-4.h-100
        .row.justify-content-center.mb-0.h-100
          .col-8.h-100
            img(src='@/assets/curso/tema2-img52.svg' alt='AvatarTop')
        p.text-center La calidad de <i>software</i> solo se logra con la contribucin de todas las personas involucradas, es decir que la calidad no es un proceso solo del equipo de aseguramiento de la calidad de <i>software</i>, sino ms bien una actividad sincrnica entre todas las personas que colaboran para el desarrollo del producto.
      .tarjeta.color-acento-contenido-3.p-4.h-100
        .row.justify-content-center.mb-0.h-100
          .col-8.h-100
            img(src='@/assets/curso/tema2-img53.svg' alt='AvatarTop')
        p.text-center La gestin y la planificacin de la calidad debe ser eficaz y previamente definido y ejecutado; por lo tanto, se deben establecer procesos, estndares y una planeacin claramente definida para poder asegurar la calidad del producto, donde contengan puntos de verificacin y un proceso claro definido que incluya qu, quin y cundo va a realizar la verificacin o medicin de la calidad.
      .tarjeta.color-acento-contenido-4.p-4.h-100
        .row.justify-content-center.mb-0.h-100
          .col-8.h-100
            img(src='@/assets/curso/tema2-img54.svg' alt='AvatarTop')
        p.text-center Se deben dirigir los esfuerzos a prevencin de defectos, es decir que una vez que un defecto es detectado adems de corregirlo se deben tomar medidas necesarias para que no vuelva a ocurrir. Es en este punto donde se tiene en cuenta lo que es el anlisis de las causas races, una vez que se encuentra un defecto se debe analizar por qu se ocasion y cul fue la causa que lo origin; por ejemplo, si es un problema en el proceso o si es un problema con las herramientas que se estn utilizando o si fue una mala definicin de los requerimientos, lo que constata que un error no solamente se soluciona o resuelve, sino que hay que indagar ms en la causa raz; porque de no hacerlo en el futuro se va a presentar el mismo problema.
      .tarjeta.color-acento-contenido-3.p-4.h-100
        .row.justify-content-center.mb-0
          .col-8
            img(src='@/assets/curso/tema2-img55.svg' alt='AvatarTop')
        p.text-center Hay que reforzar los sistemas de deteccin y eliminacin de defectos durante las etapas iniciales, lo que reafirma que eliminando los defectos en las etapas de requerimientos y diseo, estamos asegurando que esos defectos no van a ser replicados en las etapas subsiguientes, por ende es menos costoso detectar y corregir los defectos en las etapas iniciales, por ejemplo si se detecta un defecto en los requerimientos o en el diseo del <i>software</i>, lo ms posible es que se tenga que corregir documentos o diagramas, pero si se espera a la etapa de desarrollo o cuando el <i>software</i> ya est en produccin, tenemos que volver hacia atrs en todas las fases del ciclo de vida del desarrollo del <i>software</i>.
      .tarjeta.color-acento-contenido-4.p-4.h-100
        .row.justify-content-center.mb-0
          .col-8
            img(src='@/assets/curso/tema2-img56.svg' alt='AvatarTop')
        p.text-center La calidad, al igual que la productividad, costos y los plazos de entrega estn al mismo nivel, lo que quiere decir que el cumplimiento de los plazos de entrega y mantenerse dentro del presupuesto tambin son variables que se evalan dentro del proceso de aseguramiento de calidad de <i>software</i>, pues de que sirve un producto sin defectos cuando ha costado mucho ms de lo presupuestado y se ha entregado cuando ya no ser til.
      .tarjeta.color-acento-contenido-3.p-4.h-100
        .row.justify-content-center.mb-0
          .col-8
            img(src='@/assets/curso/tema2-img57.svg' alt='AvatarTop')
        p.text-center Es esencial la participacin de la direccin, que ha de propiciar la calidad, esto quiere decir que la direccin debe ser el primer interesado en asegurar la calidad y de proveer las directrices, herramientas y el apoyo necesarios para que se pueda lograr, por lo tanto si no hay apoyo de la direccin para implementar y ejecutar el proceso de aseguramiento de calidad del <i>software</i>, ser difcil que se pueda llevar a cabo la calidad y por ende los productos no van a salir con la calidad requerida.


    p.mb-5 Es importante reconocer los factores que afectan o determinan la calidad del <i>software</i>, los cuales se definen a continuacin:

    AcordionA(tipo="b" clase-tarjeta="tarjeta color-acento-contenido-2 px-5").mb-5
      .row(titulo="Correccin:")
        .col
          p.mb-0 Nivel en que un <i>software</i> satisface las especificaciones y logra los objetivos propuestos por el cliente, lo anterior responde a la pregunta: el <i>software</i> hace lo que yo quiero?
      .row(titulo="Fiabilidad")
        .col
          p.mb-0 Nivel en que se puede esperar que un programa realice sus funciones esperadas con la precisin requerida, lo anterior responde a la pregunta: el sistema est apto para usarse?
      .row(titulo="Eficiencia")
        .col
          p.mb-0 Cantidad de recursos de mquina y de cdigo requeridos por un programa para efectuar sus funciones, este factor responde a la pregunta: se ejecutar en mi <i>hardware</i> con eficiencia?
      .row(titulo="Integridad")
        .col
          p.mb-0 Nivel en el que puede controlarse el acceso al <i>software</i> o a los datos por personal que no es autorizado, este factor responde a la pregunta: el <i>software</i> es seguro?
      .row(titulo="Seguridad")
        .col
          p.mb-0 Disponibilidad de instrumentos que permiten controlar y proteger los programas y los datos.
      .row(titulo="Reusabilidad")
        .col
          p.mb-0 Nivel de trabajo solicitado para que el <i>software</i> o alguna de sus partes pueda reutilizar en otras aplicaciones. Este factor responde a la pregunta: podr reutilizar alguna parte del programa para construir otro proyecto?
      .row(titulo="Exactitud")
        .col
          p.mb-0 Nivel de precisin de los clculos y del control que tiene el programa al ejecutar sus funciones.
      .row(titulo="Completitud")
        .col
          p.mb-0 Nivel en que se ha obtenido una total implementacin de las funciones solicitadas.
      .row(titulo="Flexibilidad")
        .col
          p.mb-0 Trabajo necesario para modificar un programa que ya est en funcionamiento u operacin. Este factor responde a la pregunta: puede ser modificado de manera fcil?
      .row(titulo="Portabilidad")
        .col
          p.mb-0 Trabajo necesario para migrar el programa desde un <i>hardware</i> o un entorno de sistemas a otro ambiente u otro <i>hardware</i>, este factor responde a la pregunta: podr usarlo en otra mquina o con otra configuracin distinta a la mquina donde se desarroll?
      .row(titulo="Consistencia")
        .col
          p.mb-0 Consiste en utilizar un diseo semejante y tcnicas de documentacin en el transcurso del proyecto de desarrollo de <i>software</i>.
      .row(titulo="Facilidad de uso")
        .col
          p.mb-0 Trabajo necesario para aprender a utilizar un programa y que adems sabe preparar su entrada e interpretar su salida. Est diseado para ser utilizado de manera fcil?
      .row(titulo="Facilidad de mantenimiento")
        .col
          p.mb-0 Trabajo necesario para ubicar y arreglar un error en un programa, este factor responde a la pregunta: permite ser corregido de manera fcil?
      .row(titulo="Facilidad de prueba")
        .col
          p.mb-0 Nivel de trabajo requerido para probar un programa de forma que asegure la ejecucin de las funciones solicitadas, permite ser probado de manera fcil?
      .row(titulo="Facilidad de interoperabilidad")
        .col
          p.mb-0 Nivel de trabajo necesario para acoplar un sistema a otro, lo anterior responde a la pregunta: podr interactuar con otros sistemas?
      .row(titulo="Facilidad de auditora")
        .col
          p.mb-0 Se concentra en la facilidad en que se puede comprobar la conformidad con los estndares.
      .row(titulo="Normalizacin de las comunicaciones")
        .col
          p.mb-0 Nivel en que se utiliza el ancho de banda, las interfaces estndar y los protocolos.
      .row(titulo="Tolerancia a los errores")
        .col
          p.mb-0 Percance que se ocasiona cuando el programa encuentra un error.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.mb-5.mb-lg-0
        p.mb-0 Cuando se califica la calidad de un producto se debe elegir los factores que van a ser estimados como requerimientos; por lo tanto, para realizar esta eleccin se debe tener en cuenta las propiedades particulares de la aplicacin a desarrollar o de su ambiente, como ejemplo podemos observar que si la aplicacin se desarrolla para un ambiente en el que el <i>hardware</i> evoluciona rpidamente; entonces, se debe tener en cuenta el factor de portabilidad, por otro lado si se espera que las especificaciones del sistema cambien frecuentemente entonces el factor flexibilidad va a ser importante.
          br
          br
          | Otro punto que se debe tener en cuenta es el costo de los factores de calidad frente al beneficio que van a proporcionar (anlisis de costo/beneficio), es decir que si agregamos todos los factores implicara un incremento amplio de los costos, en lugar de lo anterior es prudente seleccionar los factores que logren contribuir el mayor valor posible al producto.
          br
          br
          | Los costos de calidad son importantes para conseguir productos de calidad, por lo que se establecen actividades para prevenir, mejorar o detectar la baja calidad que puede haber en los productos <i>software</i>.
