            p.mb-0 Cabe aclarar que la calidad de <i>software</i> puede medirse una vez en la fase de implementacin o terminado completamente o cuando el producto est en produccin, no obstante, este hecho implicara amplios costos para la compaa de <i>software</i> a diferencia de detectar y resolver los problemas en las fases iniciales como el diseo o en los requerimientos que generara una reduccin de los costos en cuanto a los cambios a realizar. Por lo tanto, es necesario tener en cuenta el proceso de <I>SQA</I> durante las etapas iniciales de la construccin del producto, puesto que es donde se definen las caractersticas que el producto debe garantizar para satisfacer las necesidades implcitas y explcitas. Por lo tanto, se debe tener en cuenta que si el diseo y los requerimientos no estn correctamente definidos, es decir que estn ambiguos, incompletos o son incoherentes, en este momento va a ser muy costosa su correccin.

    p.mb-5 Si se detectan los errores en las primeras fases como de requerimientos o diseo los costos son menores debido a que lo ms posible es que se deba modificar bsicamente documentos; en cambio s se detecta un defecto en las pruebas de aceptacin o en produccin, lo ms seguro es que se deban involucrar prcticamente a todo el equipo porque posiblemente se deba regresar hasta la primera fase que es de requerimientos; por ende, el costo de corregir defectos se incrementa debido a que todo el trabajo que va a tener que realizar el equipo nuevamente seria cargado al costo del proyecto. Debido a lo anterior es muy importante que el Aseguramiento de la Calidad de <i>Software</i> (<I>SQA</I>) comience en las primeras etapas del desarrollo de <i>software</i>.

