        .row.justify-content-center
          .col-9
            h2.text-center Problemas
            ul.lista-ul--color
              li
                i.lista-ul__vineta
                p.mb-0 Es difcil institucionalizar en organizaciones pequeas, en las que no estn disponibles los recursos necesarios para realizar las actividades del Aseguramiento de la Calidad de <i>Software</i> (<I>SQA</I>).
              li
                i.lista-ul__vineta
                p.mb-0 Representa un cambio cultural, y el cambio nunca es fcil de aceptar e implementar.
              li
                i.lista-ul__vineta
                p.mb-0 Requiere un gasto que, de otro modo, nunca se hubiera destinado explcitamente a la ingeniera del <i>software</i> o al aseguramiento de la calidad.

    p.mb-5 Para que el proceso del Aseguramiento de la Calidad de <i>Software</i> (<I>SQA</I>) se aplique correctamente, se deben aplicar estas tres etapas:

    .row.justify-content-center.mb-5
      .col-lg-4
        figure
          img(src="@/assets/curso/tema2-img17.svg")
      .col-lg-4
        figure
          img(src="@/assets/curso/tema2-img18.svg")
      .col-lg-4
        figure
          img(src="@/assets/curso/tema2-img19.svg")

    p.mb-5 En el Aseguramiento de la Calidad de <i>Software</i> usualmente se pueden trabajar con cuatro roles; no obstante, un analista puede ejercer el rol de diseador y <i>tester</i>:

    .row.mb-5
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/tema2-img20.svg' alt='AvatarTop')
          .tarjeta.color-acento-contenido-3
            .p-4
              p.mb-0
                strong Gerente de pruebas:
                |  tiene la responsabilidad de lograr el xito del esfuerzo de las pruebas, es el responsable de planificar, gestionar los recursos y solucionar los problemas que se presentan dentro del equipo de trabajo.
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/tema2-img21.svg' alt='AvatarTop')
          .tarjeta.color-acento-contenido-4
            .p-4
              p.mb-0
                strong Analista de pruebas:
                |  inicialmente este rol identifica y define las pruebas necesarias, por lo tanto, va a indicar el alcance de lo que es necesario probar.
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/tema2-img22.svg' alt='AvatarTop')
          .tarjeta.color-acento-contenido-3
            .p-4
              p.mb-0
                strong Diseador de pruebas:
                |  es el responsable de definir hacia dnde se enfocan las pruebas, as como la identificacin de tcnicas y recursos requeridos.
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/tema2-img23.svg' alt='AvatarTop')
          .tarjeta.color-acento-contenido-4
            .p-4
              p.mb-0
                strong Tester:
                |  es el responsable de realizar las pruebas requeridas, as como de registrar los resultados de las pruebas.

    p.mb-5 Los productos que resultan del proceso de desarrollo de <i>software</i> son identificados como <strong>artefactos</strong>; por ejemplo, el cdigo fuente, defectos, plan y <i>script</i> de pruebas.
      br
      br
      | El Aseguramiento de la Calidad de <i>Software</i> se compone de artefactos especficos que se forman en las diversas etapas del proceso de <I>SQA</I>, como se muestra en el siguiente recurso de aprendizaje:

    .row.justify-content-center
      .col-lg-10
        ImagenInfografica.xl.extra-text.color-acento-contenido.mb-5
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/tema2-img24.svg', alt='Texto que describa la imagen')

          .tarjeta.color-primario.p-4(x="36.6%" y="71.6%" )
            h5 Planificacin
            ul.lista-ul--color
              li
                i.fas.fa-code
                p.mb-0
                  strong Plan de pruebas:
                  |  documento que describe un enfoque sistemtico para probar un sistema.
              li
                i.fas.fa-code
                p.mb-0
                  strong Informe de resultados de pruebas:
                  |  organiza y presenta un anlisis resumido de los resultados de las pruebas para su auditora y evaluacin. Este informe debe incluir un listado de defectos detectados, la cantidad y tipos de pruebas elaboradas, as como su complejidad y los tipos de pruebas realizadas.
          .tarjeta.color-primario.p-4(x="69.8%" y="31%" )
            h5 Construccin
            ul.lista-ul--color
              li
                i.fas.fa-code
                p.mb-0
                  strong Caso de prueba:
                  |  conjunto de condiciones con las cuales un <i>tester</i> debe determinar si un sistema funciona segn lo requerido.
              li
                i.fas.fa-code
                p.mb-0
                  strong <i>Script</i> de prueba:
                  |  recopilacin de instrucciones que se realizan al <i>software</i> sujeto a prueba para verificar que el sistema funciona de acuerdo con lo requerido; hay <i>scripts</i> de pruebas automatizadas y manuales.
          .tarjeta.color-primario.p-4(x="87.8%" y="61%" )
            h5 Ejecucin
            ul.lista-ul--color
              li
                i.fas.fa-code
                p.mb-0
                  strong Resultado de ejecucin de prueba:
                  |   recopilacin de la informacin con los resultados de la ejecucin de cada <i>script</i> de prueba, especificando si tuvieron una representacin exitosa o con problemas respecto al comportamiento mostrado por la aplicacin y los resultados requeridos.
              li
                i.fas.fa-code
                p.mb-0
                  strong Defectos:
                  |  un error produce un defecto y un defecto produce una falla.

    Separador

    #t_2_2.titulo-segundo.color-acento-contenido
      h2 2.2  Implementacin del plan de pruebas

    p.mb-5 El plan de pruebas es un documento que describe un conjunto de procedimientos, tcnicas y normas para probar un sistema.
      br
      br
      | En la siguiente tabla se describen los elementos que contiene un plan de pruebas:

    .titulo-sexto.color-acento-contenido
      h5 Tabla 1
      span Elementos de un plan de pruebas

    .tabla-b.color-acento-botones.mb-5
      .tabla-b__header
      table
        tr
          th Nombre y versin
          td Debe contener el nombre del sistema a probar, el nombre del documento y la versin.
        tr
          th Introduccin
          td Especificar en qu consiste el sistema y cual sistema se va a probar. Tambin se incluye y describe el alcance de las pruebas.
        tr
          th Pruebas planificadas
          td En esta parte se listan los tipos de prueba a realizar y los requerimientos a probar en el proceso. Especifica y describe las caractersticas a probar y las que no se van a probar.
        tr
          th Estrategias de prueba
          td Representa el enfoque recomendado para la comprobacin de las aplicaciones.
        tr
          th Criterios de entrada y salida
          td En esta parte se especifica los criterios de entrada y salida de la aplicacin al rea de Aseguramiento de Calidad de <i>Software</i> (<I>SQA</I>).

    .row.justify-content-center.mb-5
      .col-lg-10
        .tarjeta.color-primario-degrade-2.p-3
          .row.justify-content-around.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/tema2-img25.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.mb-1.text-white Anexo Ejemplo Diseo Plan de Pruebas
                  p.text-small.text-white Para un mejor entendimiento de cmo construir e implementar el plan de pruebas, ver el anexo que contiene un ejemplo prctico y cmo se relacionan los elementos requeridos.
                .col-sm-auto
                  a.boton.color-acento-botones.texto-blanco(:href="obtenerLink('/downloads/Anexo_Ejemplo_Diseno_Plan_de_Pruebas_CF02_M6.pdf')" target="_blank")
                    span Descargar
                    i.fas.fa-file-download

    p.mb-5 La estructuracin de un caso de prueba se convierte en una actividad sin dificultad, si tenemos la informacin necesaria para su proceso de elaboracin, as mismo al momento de verificar un <i>software</i> es de mucho aporte, puesto que se transforma en una herramienta esencial en el proceso de registro, seguimiento y control.
      br
      br
      | A continuacin, se nombran los elementos principales que debe contener un caso de prueba:

    .titulo-sexto.color-acento-contenido
      h5 Tabla 2
      span Elementos de caso de prueba

    .tabla-b.color-acento-botones.mb-5
      .tabla-b__header
      table
        tr
          th Identificador
          td Puede ser alfanumrico o numrico.
        tr
          th Nombre
          td Nombre del caso de prueba de manera concisa.
        tr
          th Descripcin
          td Objetivo del caso de prueba, tambin describe que probara, en ciertas ocasiones se incluye el ambiente de pruebas.
        tr
          th Numero de orden Ejecucin
          td Orden en la cual se ejecuta el caso de prueba, en la situacin de que se tengan mltiples casos de prueba.
        tr
          th Requerimiento asociado
          td Si se plantea un caso de prueba se debe saber a qu requerimiento va asociado para mantener la trazabilidad.
        tr
          th Precondicin
          td Estado en el cual se debe encontrar el sistema antes de comenzar la prueba
        tr
          th Postcondicin
          td El estado en que encontrarse el sistema luego de ejecutar la prueba.
        tr
          th Resultado esperado
          td Objetivo que debe ser alcanzado posterior a la ejecucin de la prueba.

    .row.justify-content-center.mb-5
      .col-lg-10
        .tarjeta.color-primario-degrade-2.p-3
          .row.justify-content-around.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/tema2-img25.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.mb-1.text-white Redaccin  Caso de Prueba
                  p.text-small.text-white Para aprender cmo redactar y construir un caso de prueba vea el siguiente anexo
                .col-sm-auto
                  a.boton.color-acento-botones.texto-blanco(:href="obtenerLink('/downloads/Anexo_Redaccion_Caso_de_Prueba_CF02_M6.pdf')" target="_blank")
                    span Descargar
                    i.fas.fa-file-download

    p.mb-5 Redactar el <i>script</i> de pruebas es un artefacto responsabilidad del <i>tester</i>, siendo este quien lo escribe y lo ejecuta. A continuacin, se nombran los elementos principales que debe contener un <i>script</i> de prueba:

    .titulo-sexto.color-acento-contenido
      h5 Tabla 3
      span Elementos para el <i>Script</i> de prueba

    .tabla-b.color-acento-botones.mb-5
      .tabla-b__header
      table
        tr
          th Nombre
          td Identificador nico para el <i>script</i>.
        tr
          th descripcin
          td Describe el objetivo del <i>script</i> de pruebas.
        tr
          th Pasos
          td Acciones que se van ejecutando en la aplicacin.
        tr
          th Puntos de verificacin
          td Se comprueban aquellos puntos donde se verifica si la aplicacin se est comportando de la forma esperada.<br>Son las observaciones puntuales que se realizan durante la ejecucin de las pruebas para verificar que el producto cumple con los requerimientos especficos.
        tr
          th Resultado esperado
          td Por cada punto de verificacin puede haber un resultado esperado asociado.

    .row.justify-content-center.mb-5
      .col-lg-10
        .tarjeta.color-primario-degrade-2.p-3
          .row.justify-content-around.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/tema2-img25.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.mb-1.text-white Redaccion <i>Script</i> De Prueba
                  p.text-small.text-white Para aprender cmo redactar y construir un <i>script</i> de prueba ver el siguiente anexo
                .col-sm-auto
                  a.boton.color-acento-botones.texto-blanco(:href="obtenerLink('/downloads/Anexo_Ejemplo_Redaccion_Script_De_Prueba_CF02_M6.pdf')" target="_blank")
                    span Descargar
                    i.fas.fa-file-download

    Separador

    #t_2_3.titulo-segundo.color-acento-contenido
      h2 2.3  Configuracin del ambiente de pruebas

    p.mb-5 El ambiente de pruebas es una configuracin de <i>software</i> y <i>hardware</i> en el que el equipo de pruebas va a realizar la prueba del producto de <i>software</i> que ha sido desarrollado. La gestin de los ambientes de pruebas es de gran importancia, puesto que asegura que el <i>software</i> sea llevado a produccin con los niveles de calidad ptimos.

    TabsA.color-acento-botones.mb-5
      .tarjeta.tarjeta--color-secundario.p-4(titulo="Caractersticas de los<br> ambientes de pruebas")
        h4 Caractersticas de los ambientes de pruebas
        .row.justify-content-center
          .col-lg-8
            ul.lista-ul--color
              li
                i.fas.fa-code
                p.mb-0 El equipo de pruebas debe compartir un servidor en el cual va a residir el ambiente de pruebas, por lo general en una mquina distinta a la que utiliza el desarrollador.
              li
                i.fas.fa-code
                p.mb-0 Es recomendable que no se usen direcciones IP, sino ms bien nombres de dominio distintos a los de desarrollo y produccin, por lo que podra crear desorden o confusin en el equipo de pruebas.
              li
                i.fas.fa-code
                p.mb-0 Preferiblemente el ambiente de pruebas debe ser lo ms parecido a uno de produccin, que incluya herramientas de control de versiones, aplicaciones cliente/servidor, bases de datos, cuentas de usuario del sistema operativo, <i>clusters</i>, configuracin de servidores, el mismo sistema gestor de bases de datos instalado tanto en desarrollo como en produccin e instalar en un computador o servidor diferente al del ambiente de pruebas las herramientas de gestin de incidencias y casos de prueba.
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2-img26.svg")
      .tarjeta.tarjeta--color-secundario.p-4(titulo="Restricciones en los<br> ambientes de pruebas")
        h4 Restricciones en los ambientes de pruebas
        .row.justify-content-center
          .col-lg-8
            ul.lista-ul--color
              li
                i.fas.fa-code
                p.mb-0 Los desarrolladores deben tener desactivados los privilegios relacionados con el acceso de modificacin en los ambientes de pruebas.
              li
                i.fas.fa-code
                p.mb-0 El desarrollador no debe gestionar directamente el ambiente de pruebas, si no el administrador.
              li
                i.fas.fa-code
                p.mb-0 Requiere de control de versiones.
              li
                i.fas.fa-code
                p.mb-0 Instalar una versin en produccin, una vez que ha sido probada en el ambiente de pruebas.
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2-img27.svg")
      .tarjeta.tarjeta--color-secundario.p-4(titulo="Procedimientos para la <br>gestin de ambientes<br> de pruebas")
        h4 Procedimientos para la gestin de ambientes de pruebas
        .row.justify-content-center
          .col-lg-8
            ul.lista-ul--color
              li
                i.fas.fa-code
                p.mb-0 Todas las etapas de pruebas funcionales y no funcionales requieren de ambientes de pruebas, por lo que es necesario definir en cada fase de los proyectos procedimientos especficos.
              li
                i.fas.fa-code
                p.mb-0 Es recomendable que el gerente de cambios u operaciones sea diferente al del ambiente de produccin, por lo que tiene el trabajo de planear y operar.
              li
                i.fas.fa-code
                p.mb-0 Especificar los requerimientos de configuracin, infraestructura y herramientas necesarias.
              li
                i.fas.fa-code
                p.mb-0 La documentacin de requerimientos de los proyectos debe incluir los requerimientos de ambientes.
              li
                i.fas.fa-code
                p.mb-0 La utilizacin y disponibilidad de los ambientes de pruebas deben ser previamente planificados, del mismo modo el equipo de pruebas debe incluir en la planeacin de los requerimientos las restricciones mencionadas.
              li
                i.fas.fa-code
                p.mb-0 Se deben tener en cuenta procedimientos de gestin de soporte de ambientes.
              li
                i.fas.fa-code
                p.mb-0 Se deben establecer procedimientos que se puedan auditar para la preparacin de datos de prueba, accesos mltiples, actualizaciones, instalaciones, contar con herramientas de <i>debugging</i> y procesos de elaboracin, asistencia y conectividad a los equipos de pruebas de todas las etapas.

          .col-lg-4
            figure
              img(src="@/assets/curso/tema2-img28.svg")

    .row.justify-content-center
      .col-auto
        .titulo-sexto.color-acento-contenido
          h5 Figura 4
          br
          span Ambientes de pruebas de <i>software</i>
    figure.mb-5
      img(src="@/assets/curso/tema2-img29.svg")

    p.mb-0 En la figura anterior se pueden observar los aspectos ms importantes para tener en cuenta en la configuracin de los ambientes, elementos como definiciones, caractersticas y restricciones.

    Separador

    #t_2_4.titulo-segundo.color-acento-contenido
      h2 2.4  Ejecucin de las pruebas

    p.mb-5 Inicia creando los datos de prueba requeridos para ejecutar los casos de prueba establecidos. La ejecucin de los casos de prueba se puede realizar de forma automatizada o manual, en cualquiera de las dos formas se debe registrar y documentar los fallos detectados en el sistema y su posterior correccin.
      br
      br
      | En el siguiente recurso de aprendizaje se puede observar el flujo de las actividades en relacin con la ejecucin de pruebas:

    h3.titulo-tercero Flujo de las actividades de la ejecucin de pruebas

    ImagenInfografica.xl.extra-text.color-secundario.mb-5
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/tema2-img30.svg', alt='Texto que describa la imagen')

      .tarjeta.color-primario.p-4(x="7%" y="66.4%" )
        p.mb-0
          strong 1. Planificar calendario detallado de ejecucin:
          |    definir el calendario que especifique la ejecucin de los casos de prueba, teniendo en cuenta el plan y los casos de prueba.
      .tarjeta.color-primario.p-4(x="29.2%" y="66.4%" )
        p.mb-0
          strong 2. Verificar entorno de pruebas:
          |    verificar que el ambiente de pruebas tenga un funcionamiento correcto.
      .tarjeta.color-primario.p-4(x="49.9%" y="66.4%" )
        p.mb-0
          strong 3. Integrar los datos de pruebas:
          |    realizar una recoleccin de los datos de prueba a utilizar en la ejecucin de pruebas.
      .tarjeta.color-primario.p-4(x="70.4%" y="66.4%" )
        p.mb-0
          strong 4. Evaluar criterios de entrada:
          |    analizar los criterios de entrada contenidos en el plan de pruebas, de tal forma que se pueda verificar la ejecucin de las pruebas.
      .tarjeta.color-primario.p-4(x="88.4%" y="66.4%" )
        p.mb-3
          strong 5. Ejecutar las pruebas:
          |    incluye las siguientes actividades:
        ul.lista-ul--color
          li
            i.fas.fa-code
            p.mb-0 Ejecutar casos y <i>script</i> de prueba.
          li
            i.fas.fa-code
            p.mb-0 Analizar y registrar los resultados.
          li
            i.fas.fa-code
            p.mb-0 Registrar y reportar las incidencias encontradas

    Separador

    #t_2_5.titulo-segundo.color-acento-contenido
      h2 2.5  Creacin de plan de pruebas e integracin y entrega continua en una metodologa gil

    p.mb-5 En un proyecto gil est compuesto por iteraciones o <i>sprints</i>, cada <i>sprint</i> tiene una duracin que no es muy extensa, aproximadamente dos a cuatro semanas tal como se puede ver la siguiente figura, al final de cada iteracin ya estn definidas funcionalidades que pueden ser utilizadas por el cliente. En los proyectos agiles existen dos tipos de planificaciones <i>release</i>/entrega y <i>sprint</i>/iteracin, no obstante, estas se pueden dar en un mismo momento; es decir, que puede incluir una sola planificacin con actividades tanto del <i>release</i> como del <i>sprint</i>. No obstante, se debe tener en cuenta que en un proyecto gil las pruebas de <i>software</i> se realizan en cada iteracin.

    figure.mb-5
      img(src="@/assets/curso/tema2-img31.svg")
      figcaption Nota. Tomada de <i>Globetesting</i> (s.f.)

    .row.justify-content-center.align-items-center.no-gutters.mb-5
      .col-lg-7.box-with-degrade.color-acento-botones.radius-25.mb-5.mb-lg-0
        p.mb-0.p-5 Teniendo en cuenta las iteraciones de un <i>sprint</i>, al final de cada iteracin ya estn definidas funcionalidades que pueden ser utilizadas por el cliente. En los proyectos giles existen dos tipos de planificaciones <i>release</i>/entrega y <i>sprint</i>/iteracin; no obstante, estas se pueden dar en un mismo momento, es decir que puede incluir una sola planificacin con actividades tanto del <i>release</i> como del <i>sprint</i>. La siguiente figura lo representa.
      .col-lg-5
        figure
          img(src="@/assets/curso/tema2-img32.svg")
          figcaption Nota. Tomada de <i>Globetesting</i> (s.f.)

    p.mb-5 La metodologa de desarrollo de <i>software</i> en cascada utiliza diferentes tipos de pruebas de <i>software</i> que probaran tanto requerimientos funcionales como no funcionales de manera secuencial. No obstante, en metodologas giles especficamente en las pruebas giles, tambin se consideran varios tipos de pruebas, pero teniendo en cuenta que se utilizan <i>sprints</i> o interacciones cortas e integraciones continuas entre los equipos de desarrollo, diseo y pruebas.
      br
      br
      | En el siguiente recurso de aprendizaje se presentan los principios de las pruebas giles o Agil <i>Testing</i>:

    ImagenInfografica.xl.extra-text.color-acento-botones.mb-5
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/tema2-img33.svg', alt='Texto que describa la imagen')

      .tarjeta.color-acento-contenido.p-4(x="71%" y="14.4%" )
        p.mb-0
          strong Integracin continua:
          |   los equipos de diseo y desarrollo ejecutan pruebas continuamente, puesto que es una forma de asegurar el avance continuo del producto.
      .tarjeta.color-acento-contenido.p-4(x="74%" y="30.4%" )
        p.mb-0
          strong Entrega continua:
          |   se soporta en la integracin continua, en este punto los desarrolladores realizan un control y la cantidad de veces que se realiza la entrega continua.
      .tarjeta.color-acento-contenido.p-4(x="76%" y="46.4%" )
        p.mb-0
          strong Elaborar menos documentacin:
          |   los participantes de los equipos en metodologas giles pueden crear listas y tomar notas para centrarse en probar el <i>software</i>, lo que permite no enfocarse en detalles secundarios que tomaran mucho tiempo.
      .tarjeta.color-acento-contenido.p-4(x="74%" y="60.4%" )
